@import url(https://fonts.googleapis.com/css?family=Baloo+Thambi+2&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.MapContainer{height:400px;padding-left:5%;padding-right:5%}.App{text-align:center;justify-content:center;font-family:'Roboto', sans-serif;color:#424242;background-color:#FAFAFA}.TableScreenContainer{margin-top:20px}.HomeScreenContainer{text-align:center;justify-content:center;padding:2% 5%}.GlobalCount{font-weight:bolder;margin:10px 0}.GlobalItemContainer{margin:50px 0}.LastUpdatedText{text-align:end;font-size:medium;font-weight:bolder;color:#424242}

th:hover, tr:hover {
    cursor: pointer;
}

.LatestCount {
    font-size: x-small;
}

.InputStyle {
    border-bottom: 1px solid #ccc;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    width: 40%;
}

.InputContainer {
    padding-bottom: 10px;
    width: 40%;
    float: right;
}

.MapVIew,.mapContainer,.mapBox{width:100%;height:100%;margin:0;padding:0;overflow:hidden;box-sizing:border-box;font-size:10px;font-family:'Baloo Thambi 2', cursive}.mapboxgl-popup{font-family:'Baloo Thambi 2', cursive;font-size:10px;padding:0;margin:0;color:#424242}.mapboxgl-popup-content{padding:1rem;margin:0}.mapboxgl-popup-content>*{margin:0 0 0.5rem;padding:0}.mapboxgl-popup-content>*:last-child{margin-bottom:0}.mapboxgl-popup-content p{border-bottom:1px solid rgba(0,0,0,0.2)}.mapboxgl-popup-content p b{font-size:1.6rem;color:#212121;padding:0 5px}.mapboxgl-popup-content img{display:block;width:3rem;height:3rem}.source{background-color:rgba(255,255,255,0.5);padding:0.2rem 2rem;font-size:1.4rem;position:absolute;top:100%;transform:translateY(-100%) translateX(-50%);left:50%}.source a{color:#4264fb;text-decoration:none}

.AreaChartsContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
}

